import { AgGridReact } from 'ag-grid-react';
import 'ag-grid-enterprise';
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';
import { FastfoodOutlined, FormatListNumbered } from '@material-ui/icons';
import Dropdown from 'react-multilevel-dropdown';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

const logos = {
  AgGrid:
    'data:image/png;base64,',
};
class ExcelExportDemo extends Component{
    constructor(props){
        super(props);
        this.state={
            columnDefs:[
                {field:'採購單號'},
                {field:'借出單號'},
                {field:'工單編號'},
                {field:'產品代號'},
                {field:'PCB No'},
                {field:'換貨'},
                {field:'備品'},
                {field:'進退'},
                {field:'倉別'},
                {field:'備註'},
            ],
            defaultColDef:{
                width:150,
                resizable:true,
            },
            defaultExcelExportParams: {
                prependContent: [
                  [
                    {
                      data: {
                        type: 'String',
                        value: logos.AgGrid,
                      },
                      mergeAcross: 1,
                    },
                  ],
                ],
                rowHeight: (params) => (params.rowIndex === 1 ? 82 : 20),
                addImageToCell: (rowIndex, col, value) => {
                  if (rowIndex !== 1 || col.colId !== '採購單號') {
                    return;
                  }
                  return {
                    image: {
                      id: 'logo',
                      base64: value,
                      imageType: 'png',
                      width: 295,
                      height: 100,
                      position: { colSpan: 2 },
                    },
                  };
                },
              },
        }
    }
    onGridReady=(params)=>{
        this.gridApi = params.api;
        this.gridColumnApi = params.columnApi;
    
        fetch('https://www.ag-grid.com/example-assets/small-olympic-winners.json')
          .then((response) => response.json())
          .then((data) => params.api.setRowData(data));
      };
      onBtExport = () => {
        this.gridApi.exportDataAsExcel();
      };
      render() {
        return (
          <div style={{ width: '100%', height: '100%' }}>
            <div className="container">
              <div>
                <button className="export" onClick={() => this.onBtExport()}>
                  Export to Excel
                </button>
              </div>
              <div className="grid-wrapper">
                <div
                  id="myGrid"
                  style={{
                    height: '100%',
                    width: '100%',
                  }}
                  className="ag-theme-alpine"
                >
                <AgGridReact
                    columnDefs={this.state.columnDefs}
                    defaultColDef={this.state.defaultColDef}
                    defaultExcelExportParams={this.state.defaultExcelExportParams}
                    onGridReady={this.onGridReady}
                  />
                </div>
              </div>
            </div>
          </div>
        );
      }
}
export default ExcelExportDemo